/* Login.css */
.login-container {
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    align-items: center;
    height: 100vh;
    width: 100%;
    background-image: url('/public/background_fab.png');
    /* Imagem de fundo */
    background-size: cover;
    background-position: center;
}

/* Cabe√ßalho */
.login-header {
    display: flex;
    align-items: center;
    justify-content: flex-start;
    width: 100%;
    padding: 1rem;
    height: 5vh;
    background-color: white;
    /* Sem fundo */
}

.login-header img {
    width: 50px;
    margin-left: 0.5rem;
    margin-right: 0.5rem;
    margin-top: 0.5rem;
}

.login-header h1 {
    font-size: 1.5rem;
    font-weight: bold;
    color: #000;
    margin: 0;
}

.login-header p {
    font-size: 1.5rem;
    color: #666;
    margin: 0;
}

/* Espa√ßo Central (com imagem de fundo) */
.login-center {
    flex: 1;
    display: flex;
    justify-content: center;
    align-items: center;
    width: 100%;
}

/* Espa√ßo do Login (dentro do espa√ßo central) */
.login-box {
    background-color: white;
    padding: 1.5rem;
    border-radius: 8px;
    box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
    width: 100%;
    max-width: 300px;
    text-align: center;
}

.login-box img {
    width: 150px;
    margin-bottom: -2rem;
}

.login-title {
    font-size: 1.75rem;
    font-weight: bold;
    margin-bottom: 1rem;
    text-align: center;
}

.login-form {
    display: flex;
    flex-direction: column;
}

.login-input-group {
    margin-bottom: 1rem;
    text-align: left;
}

.login-input-group label {
    display: block;
    font-size: 0.875rem;
    color: #333;
    margin-bottom: 0.5rem;
}

.login-input {
    display: flex;
    align-items: center;
    border: 1px solid #ccc;
    border-radius: 4px;
    padding: 0.5rem;
}

.login-input input {
    flex: 1;
    border: none;
    outline: none;
    margin-left: 0.5rem;
}

.login-input .icon {
    color: #1559BF;
}

.forgot-password {
    text-align: right;
    font-size: 0.875rem;
    color: #007bff;
    margin-bottom: 1rem;
    cursor: pointer;
}

.forgot-password:hover {
    text-decoration: underline;
}

.login-button {
    background-color: #007bff;
    color: white;
    padding: 0.75rem;
    border: none;
    border-radius: 4px;
    font-size: 1rem;
    cursor: pointer;
    transition: background-color 0.3s ease;
}

.login-button:hover {
    background-color: #0056b3;
}

/* Rodap√© */
.login-footer {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    text-align: center;
    width: 100%;
    height: 5vh;
    font-size: 0.875rem;
    color: white;
    background-color: #1559BF;
    padding: 1rem 0;
}

.login-footer h1,
.login-footer h2 {
    margin: 0;
}

.register-button {
    background-color: #007bff;
    color: white;
    padding: 0.75rem;
    border: none;
    border-radius: 4px;
    font-size: 1rem;
    cursor: pointer;
    transition: background-color 0.3s ease;
    position: absolute;
    top: 18px;
    right: 18px;
}

.modal {
    position: fixed;
    z-index: 1000;
    left: 0;
    top: 0;
    width: 100%;
    height: 100%;
    overflow: auto;
    background-color: rgba(0, 0, 0, 0.5);
    display: flex;
    justify-content: center;
    align-items: center;
}

.modal-content {
    background-color: #fefefe;
    padding: 20px;
    border: 1px solid #888;
    width: 80%;
    max-width: 500px;
    border-radius: 8px;
    box-shadow: 0 5px 15px rgba(0, 0, 0, 0.3);
    animation: fadeIn 0.3s ease-in-out;
}

@keyframes fadeIn {
    from {
        opacity: 0;
        transform: scale(0.9);
    }

    to {
        opacity: 1;
        transform: scale(1);
    }
}

.close {
    color: #aaa;
    float: right;
    font-size: 28px;
    font-weight: bold;
}

.close:hover,
.close:focus {
    color: black;
    text-decoration: none;
    cursor: pointer;
}

.modal-content h2 {
    margin-top: 0;
}

.modal-content input {
    width: calc(100% - 20px);
    padding: 10px;
    margin: 10px 0;
    border: 1px solid #ccc;
    border-radius: 4px;
}

.modal-content button {
    color: white;
    padding: 10px 20px;
    margin: 10px 0;
    border: none;
    border-radius: 4px;
    cursor: pointer;
    width: 100%;
}

.confirm-button {
    background-color: #218838;
    color: white;
    padding: 10px 20px;
    margin: 10px 0;
    border: none;
    border-radius: 4px;
    cursor: pointer;
    width: 100%;
}

.confirm-button:hover {
    background-color: #45a049;
}

.cancel-button {
    background-color: #f44336;
    color: white;
    padding: 10px 20px;
    margin: 10px 0;
    border: none;
    border-radius: 4px;
    cursor: pointer;
    width: 100%;
}

.cancel-button:hover {
    background-color: #d32f2f;
}

.register-section {
    margin-top: 1rem;
    text-align: center;
}

.register-link {
    background: none;
    border: none;
    color: #007bff;
    cursor: pointer;
    text-decoration: underline;
    font-size: 1rem;
}

.register-link:hover {
    color: #0056b3;
}